import React, { Component, Fragment } from "react";
import Modal from "react-modal";
import CKEditor from "ckeditor4-react";
import { Fileupload } from "../component";

Modal.setAppElement("#root");

class BlockModModal extends Component {
  state = {
    modalIsOpen: false
  };

  constructor() {
    super();
    this.openModal = this.openModal.bind(this);
    this.afterOpenModal = this.afterOpenModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  openModal() {
    this.setState({ modalIsOpen: true });
  }

  afterOpenModal() {
    //  /  /     .
  }

  closeModal() {
    this.setState({ modalIsOpen: false });
  }

  render() {
    return (
      <Fragment>
        <button className="btn btn-warning sm" onClick={this.openModal}>
          <i className="ico-pencil2" />
          <span></span>
        </button>
        <Modal
          isOpen={this.state.modalIsOpen}
          onAfterOpen={this.afterOpenModal}
          onRequestClose={this.closeModal}
          className="default-modal"
          overlayClassName="default-overlay"
        >
          <div className="header-modal">
            <button className="close-modal" onClick={this.closeModal}>
              <i className="ico-cross" />
            </button>
            <h2 className="title-modal"></h2>
          </div>
          <div className="contents-modal">
            {/*   */}
            <div className="table horizon">
              <table className="table-defaults tables-hor table table-bordered">
                <tbody>
                  <tr>
                    <th scope="row" style={{ width: "15%" }}>
                      <span> </span>
                    </th>
                    <td>
                      <div className="select">
                        <select>
                          <option value="1"></option>
                          <option value="2"></option>
                          <option value="3"></option>
                          <option value="4"> </option>
                          <option value="5"></option>
                        </select>
                        <span className="bullet">
                          <svg
                            height="20"
                            width="20"
                            viewBox="0 0 20 20"
                            aria-hidden="true"
                            focusable="false"
                          >
                            <path d="M4.516 7.548c0.436-0.446 1.043-0.481 1.576 0l3.908 3.747 3.908-3.747c0.533-0.481 1.141-0.446 1.574 0 0.436 0.445 0.408 1.197 0 1.615-0.406 0.418-4.695 4.502-4.695 4.502-0.217 0.223-0.502 0.335-0.787 0.335s-0.57-0.112-0.789-0.335c0 0-4.287-4.084-4.695-4.502s-0.436-1.17 0-1.615z" />
                          </svg>
                        </span>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <th scope="row">
                      <span></span>
                    </th>
                    <td>
                      <CKEditor
                        config={{
                          toolbar: [], //    
                          startupMode: "source", //      
                          resize_enabled: false //   
                        }}
                        data=" "
                      />
                      <p className="form-Feedback">
                        <i className="ico-cancel-circle" />
                        <span>  .</span>
                      </p>
                    </td>
                  </tr>
                  <tr>
                    <th scope="row">
                      <span></span>
                    </th>
                    <td>
                      <Fileupload />
                      <p className="form-Feedback">
                        <i className="ico-cancel-circle" />
                        <span>  .</span>
                      </p>
                    </td>
                  </tr>
                  <tr>
                    <th scope="row">
                      <span></span>
                    </th>
                    <td>
                      <input
                        type="text"
                        className="form-input"
                        title="  "
                        placeholder=" ."
                      />
                      <p className="form-Feedback">
                        <i className="ico-cancel-circle" />
                        <span>  .</span>
                      </p>
                    </td>
                  </tr>
                  <tr>
                    <th scope="row">
                      <span> </span>
                    </th>
                    <td>
                      <input
                        type="text"
                        className="form-input"
                        title="  "
                        placeholder="  ."
                      />
                      <p className="form-Feedback">
                        <i className="ico-cancel-circle" />
                        <span>  .</span>
                      </p>
                    </td>
                  </tr>
                  <tr>
                    <th scope="row">
                      <span></span>
                    </th>
                    <td>
                      <div className="select">
                        <select>
                          <option value="10"></option>
                          <option value="30"></option>
                        </select>
                        <span className="bullet">
                          <svg
                            height="20"
                            width="20"
                            viewBox="0 0 20 20"
                            aria-hidden="true"
                            focusable="false"
                          >
                            <path d="M4.516 7.548c0.436-0.446 1.043-0.481 1.576 0l3.908 3.747 3.908-3.747c0.533-0.481 1.141-0.446 1.574 0 0.436 0.445 0.408 1.197 0 1.615-0.406 0.418-4.695 4.502-4.695 4.502-0.217 0.223-0.502 0.335-0.787 0.335s-0.57-0.112-0.789-0.335c0 0-4.287-4.084-4.695-4.502s-0.436-1.17 0-1.615z" />
                          </svg>
                        </span>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div className="btn-group modal">
            <div>
              <button className="btn btn-info">
                <i className="ico-checkmark" />
                <span></span>
              </button>
              <button className="btn btn-danger" onClick={this.closeModal}>
                <i className="ico-cross" />
                <span></span>
              </button>
            </div>
          </div>
        </Modal>
      </Fragment>
    );
  }
}

export default BlockModModal;
