import React, { Component } from "react";
import {
  Breadcrumb,
  Pagination,
  TableV,
  TableH,
  Chatbot,
  Select
} from "../unit";
import {
  Selectbox,
  SelectboxMulti,
  Dragndrop,
  Modal,
  Modalalert,
  Modalconfirm
} from "../component";

class Main extends Component {
  render() {
    return (
      <div className="main">
        <h2></h2>
        <Breadcrumb />

        <h2> (Select:system)</h2>
        <div className="box-component">
          <Select />
        </div>

        <h2> (Select:react-select)</h2>
        <div className="box-component">
          <Selectbox
            selectopt={[
              { value: 10, label: " 10" },
              { value: 20, label: " 20" },
              { value: 50, label: " 50" }
            ]}
          />
        </div>

        <h2> (Select:react-select | Multi)</h2>
        <div className="box-component">
          <SelectboxMulti
            selectopt={[
              { value: "guestBlock", label: "guestBlock", badge: "v1" },
              { value: "memberBlock", label: "memberBlock", badge: "v2" },
              { value: "contractBlock", label: "contractBlock", badge: "v3" }
            ]}
          />
        </div>

        <h2>(input)</h2>
        <div className="box-component">
          <div className="box-form">
            <input
              type="text"
              className="form-input"
              title="  "
              placeholder=" ."
            />
            <p className="form-Feedback">
              <i className="ico-cancel-circle" />
              <span>     </span>
            </p>
          </div>
          <div className="box-form">
            <input
              type="password"
              className="form-input"
              title="  "
              placeholder=" ."
            />
            <p className="form-Feedback">
              <i className="ico-cancel-circle" />
              <span>     </span>
            </p>
          </div>
          <div className="box-form">
            <input
              type="search"
              className="form-input"
              title="  "
              placeholder=" ."
            />
            <p className="form-Feedback">
              <i className="ico-cancel-circle" />
              <span>     </span>
            </p>
          </div>
        </div>

        <h2>  </h2>
        <div className="box-component">
          <div className="board-group">
            <div className="select numbering">
              <select>
                <option value="10"> 10</option>
                <option value="30"> 20</option>
                <option value="50"> 50</option>
              </select>
              <span className="bullet">
                <svg
                  height="20"
                  width="20"
                  viewBox="0 0 20 20"
                  aria-hidden="true"
                  focusable="false"
                >
                  <path d="M4.516 7.548c0.436-0.446 1.043-0.481 1.576 0l3.908 3.747 3.908-3.747c0.533-0.481 1.141-0.446 1.574 0 0.436 0.445 0.408 1.197 0 1.615-0.406 0.418-4.695 4.502-4.695 4.502-0.217 0.223-0.502 0.335-0.787 0.335s-0.57-0.112-0.789-0.335c0 0-4.287-4.084-4.695-4.502s-0.436-1.17 0-1.615z" />
                </svg>
              </span>
            </div>
            <div className="searching">
              <div className="select">
                <select>
                  <option value="0"> + </option>
                  <option value="1"></option>
                  <option value="2"></option>
                </select>
                <span className="bullet">
                  <svg
                    height="20"
                    width="20"
                    viewBox="0 0 20 20"
                    aria-hidden="true"
                    focusable="false"
                  >
                    <path d="M4.516 7.548c0.436-0.446 1.043-0.481 1.576 0l3.908 3.747 3.908-3.747c0.533-0.481 1.141-0.446 1.574 0 0.436 0.445 0.408 1.197 0 1.615-0.406 0.418-4.695 4.502-4.695 4.502-0.217 0.223-0.502 0.335-0.787 0.335s-0.57-0.112-0.789-0.335c0 0-4.287-4.084-4.695-4.502s-0.436-1.17 0-1.615z" />
                  </svg>
                </span>
              </div>
              <div className="searchbox">
                <input
                  type="search"
                  className="form-input"
                  title="  "
                  placeholder=" ."
                />
                <button className="btn">
                  <i className="ico-search" />
                </button>
              </div>
            </div>
          </div>

          <div className="board-group">
            <div className="numbering">
              <SelectboxMulti
                selectopt={[
                  { value: "guestBlock", label: "guestBlock", badge: "v1" },
                  { value: "memberBlock", label: "memberBlock", badge: "v2" },
                  {
                    value: "contractBlock",
                    label: "contractBlock",
                    badge: "v3"
                  }
                ]}
              />
            </div>
          </div>
        </div>

        <h2> Ver</h2>
        <div className="box-component">
          <TableV />
        </div>

        <h2> Hor</h2>
        <div className="box-component">
          <TableH />
        </div>

        <h2></h2>
        <h3> </h3>
        <div className="box-component">
          <button className="btn btn-info">
            <i className="ico-list" />
            <span></span>
          </button>
          <button className="btn btn-info">
            <i className="ico-plus" />
            <span></span>
          </button>
          <button className="btn btn-warning">
            <i className="ico-pencil2" />
            <span></span>
          </button>
          <button className="btn btn-danger">
            <i className="ico-bin" />
            <span></span>
          </button>
          <button className="btn btn-info">
            <i className="ico-checkmark" />
            <span></span>
          </button>
          <button className="btn btn-secondary">
            <i className="ico-cross" />
            <span></span>
          </button>
          <button className="btn btn-info">
            <i className="ico-image" />
            <span> </span>
          </button>
        </div>
        <h3> </h3>
        <div className="box-component">
          <button className="btn btn-info sm">
            <i className="ico-plus" />
            <span></span>
          </button>
          <button className="btn btn-danger sm">
            <i className="ico-bin" />
            <span></span>
          </button>
          <button className="btn btn-info sm">
            <i className="ico-image" />
            <span></span>
          </button>
        </div>
        <h3></h3>
        <div className="box-component">
          <Pagination />
        </div>

        <h2> </h2>
        <h3> </h3>
        <div className="btn-group">
          <Pagination />
          <button className="btn btn-info">
            <i className="ico-plus" />
            <span></span>
          </button>
        </div>
        <h3> </h3>
        <div className="btn-group">
          <button className="btn btn-info">
            <i className="ico-list" />
            <span></span>
          </button>
          <div>
            <button className="btn btn-warning">
              <i className="ico-pencil2" />
              <span></span>
            </button>
            <button className="btn btn-danger">
              <i className="ico-bin" />
              <span></span>
            </button>
          </div>
        </div>
        <h3></h3>
        <div className="btn-group modal">
          <div>
            <button className="btn btn-warning">
              <i className="ico-pencil2" />
              <span></span>
            </button>
            <button className="btn btn-danger">
              <i className="ico-bin" />
              <span></span>
            </button>
          </div>
        </div>

        <h2></h2>
        <h3> </h3>
        <div className="box-component">
          <span className="badge primary">Primary</span>
          <span className="badge secondary">Secondary</span>
          <span className="badge success">Success</span>
          <span className="badge use">Use</span>
          <span className="badge danger">Danger</span>
          <span className="badge warning">Warning</span>
          <span className="badge info">Info</span>
          <span className="badge light">Light</span>
          <span className="badge dark">Dark</span>
        </div>
        <h3> </h3>
        <div className="box-component">
          <span className="badge primary fill">Primary</span>
          <span className="badge secondary fill">Secondary</span>
          <span className="badge success fill">Success</span>
          <span className="badge use fill">Use</span>
          <span className="badge danger fill">Danger</span>
          <span className="badge warning fill">Warning</span>
          <span className="badge info fill">Info</span>
          <span className="badge light fill">Light</span>
          <span className="badge dark fill">Dark</span>
        </div>
        <h3> </h3>
        <div className="box-component">
          <a href="javascript:void(0);" className="badge primary">
            Primary
          </a>
          <a href="javascript:void(0);" className="badge secondary">
            Secondary
          </a>
          <a href="javascript:void(0);" className="badge success">
            Success
          </a>
          <a href="javascript:void(0);" className="badge use">
            Use
          </a>
          <a href="javascript:void(0);" className="badge danger">
            Danger
          </a>
          <a href="javascript:void(0);" className="badge warning">
            Warning
          </a>
          <a href="javascript:void(0);" className="badge info">
            Info
          </a>
          <a href="javascript:void(0);" className="badge light">
            Light
          </a>
          <a href="javascript:void(0);" className="badge dark">
            Dark
          </a>
        </div>

        <h2></h2>
        <div className="box-component">
          <div className="alert primary"> primary</div>
          <div className="alert secondary"> secondary</div>
          <div className="alert success"> success</div>
          <div className="alert danger"> danger</div>
          <div className="alert warning"> warning</div>
          <div className="alert info"> info</div>
          <div className="alert dark"> dark</div>
        </div>

        <h2></h2>
        <div className="box-component">
          <Chatbot />
        </div>

        <h2>   </h2>
        <div className="box-component">
          <Dragndrop />
        </div>

        <h2></h2>
        <div className="box-component">
          <Modal />
          <Modalalert />
          <Modalconfirm />
        </div>

        <h2></h2>
        <div className="box-component">
          <div className="spinner-roller">
            {(function() {
              let balls = [];
              for (let i = 0; i < 7; i++) {
                balls.push(<div key={i} />);
              }
              return balls;
            })()}
          </div>
        </div>
      </div>
    );
  }
}

export default Main;
