import React, { Component } from "react";
import Select, { components } from "react-select";

class SelectboxMulti extends Component {
  static defaultProps = {
    selectopt: [{ value: 0, label: "  .", badge: "v1" }]
  };

  state = {
    selectedOption: null
  };

  //  onChange  ( option  )
  changeHandler = selectedOption => {
    this.setState({ selectedOption });
    console.log(" :", selectedOption);
  };
  keydownHandler = () => {
    //  
    console.log(": keydownHandler");
  };

  render() {
    const props = this.props;
    return (
      <div className="selectboxMulti-wrap">
        <Select
          defaultValue={props.selectopt[0]}
          onChange={this.changeHandler}
          onKeyDown={this.keydownHandler}
          options={props.selectopt}
          isSearchable={true}
          placeholder=" ."
          noOptionsMessage={() => {
            return "  .";
          }}
          className="selectbox "
          isMulti
          components={{
            Option: CustomSelectOpt,
            MultiValue: CustomSelectValue
          }}
        />
      </div>
    );
  }
}

//   
class CustomSelectOpt extends Component {
  render() {
    const props = this.props;
    return (
      <components.Option {...props}>
        {props.children}
        <span className="badge danger fill">{props.data.badge}</span>
      </components.Option>
    );
  }
}

//    
class CustomSelectValue extends Component {
  render() {
    const props = this.props;
    return (
      <components.MultiValue {...props}>
        {props.children}
        <span className="badge danger fill">{props.data.badge}</span>
      </components.MultiValue>
    );
  }
}

export default SelectboxMulti;
